.container-fluid {
    padding-top: 70px;
    padding-bottom: 70px;
}
.navbar {
    background-color: #cacaca85;
    padding-top: 15px;
    padding-bottom: 15px;
    border: 0;
    border-radius: 0;
    margin-bottom: 0;
    font-size: 12px;
    letter-spacing: 5px;
}

.navbar-nav {
    flex-direction: row;
}

.navbar-header {
    margin: 10px;
}

.navbar-nav li {
    margin: 10px;
}

.navbar-nav  li:hover {
    color: #1abc9c !important;
    cursor: pointer;
}

.content-body {
    padding: 50px;
}

.footer-padding {
	padding-bottom: 60px;
}

.footer {
	position: absolute;
	text-align: center;
	bottom: 0;
	width: 100%;
	height: 60px;
	background-color: #1abc9c;
}

.footer p {
	margin-top: 25px;
	font-size: 12px;
  color: #fff;
}
